.video {
  padding: 17px 20px 10px 20px;
}

.video__subtitle {
  padding: 0 20px;
  margin-bottom: 26px;
}

.video__description {
  padding: 0 20px;
  margin-bottom: 38px;
}

.video__play {
  position: relative;
  display: block;
  width: 100%;
  min-height: 177px;
  padding: 0;
  border: none;
  cursor: pointer;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 48px;
    height: 54px;
    margin-top: -27px;
    margin-left: -24px;
    background-image: url("/img/play-button.svg");
    background-repeat: no-repeat;
  }

  &:hover,
  &:focus-visible {
    opacity: 0.6;
    outline: none;
  }

  &:active {
    opacity: 0.3;
  }
}

.video__image {
  display: block;
  width: 100%;
  object-fit: cover;
}

@media (min-width: @tablet-width) {
  .video {
    padding: 58px 0 40px 0;
  }

  .video__subtitle {
    padding: 0 60px;
    margin-bottom: 24px;
  }

  .video__description {
    padding: 0 146px;
    margin-bottom: 90px;
  }

  .video__play {
    max-width: 800px;
    min-height: 500px;
    margin: 0 auto;
  }

  .video__image {
    display: block;
    min-height: 500px;
    object-fit: cover;
  }
}

@media (min-width: @desktop-width) {
  .video {
    padding: 62px 0 40px 0;
  }

  .video__subtitle {
    padding: 0 60px;
    margin-bottom: 24px;
  }

  .video__description {
    padding: 0 370px;
    margin-bottom: 80px;
  }
}
